<template>
  <div>
    <b-navbar
      toggleable="lg"
      type="dark"
      fixed="top"
      :class="{ black: navbarScroll }"
    >
      <b-container>
        <b-navbar-brand href="/">
          <img src="@/assets/img/logo.png" />
        </b-navbar-brand>

        <b-collapse id="nav-collapse" is-nav>
          <!-- Right aligned nav items -->
          <b-navbar-nav class="ml-auto">
            <b-nav-item v-scroll-to="'#carousel-fade'">Somos Domo</b-nav-item>
            <b-nav-item v-scroll-to="'#queOfrecemos'">Que ofrecemos</b-nav-item>
            <b-nav-item v-scroll-to="'#escribenos'">escríbenos</b-nav-item>
          </b-navbar-nav>
        </b-collapse>
      </b-container>
    </b-navbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      navbarScroll: false
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
  },

  beforeDestroy() {
    window.removeEventListener("scroll", this.onScroll);
  },
  methods: {
    onScroll() {
      const currentScrollPosition = window.pageYOffset;
      if (currentScrollPosition < 0) {
        return;
      }
      this.navbarScroll = currentScrollPosition;
    }
  }
};
</script>
